.card {
  animation: fadeIn ease 0.2s;
  max-width: 100%;
}

.card:hover > .card-img {
  -webkit-filter: opacity(0%);
          filter: opacity(0%); 
}

.card-img {
  transition: 0.1s;
  -webkit-filter: opacity(25%) saturate(150%) hue-rotate(-50deg);
          filter: opacity(25%) saturate(150%) hue-rotate(-50deg);
}

.delete {
  flex: 1 1;
  display: flex;
  width: 20%;
  padding: 10px;
  justify-content: center;
  align-items: center;
  border-left: 1px solid lightgray;
  top: 50%;
  bottom: 0%
} 

.add {
  max-width: 20%;
  height: 100%;
  padding: 10px;
  justify-content: center;
  align-items: center;
}

@keyframes fadeIn {
  0% {opacity: 0;}
  100% {opacity: 1;}
}

.add-card {
  background-color: rgba(0,0,0,0);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: none;
}

.card-img {
  -webkit-filter: opacity(0%);
          filter: opacity(0%)
}

.button {
  justify-content: center;
  background:rgb(68, 130, 172);
  border-color: rgb(255, 255, 255);
}

.page {
  overflow-y: auto;
  height: 100%;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-content: center;
}

.display-user {
  text-align: center;
  padding-top: 30px;
  color: white;
  font-family: 'Overpass', sans-serif;
  font-size: 20px;
}

.single-add { 
  flex-grow: 1;
  background:rgb(84, 168, 224);
  border-color: rgb(255, 255, 255);
  color: white
}

.cols {
  padding: 30px;
}


.page {
  height: 100%;
  width: 100%;
  position: fixed;
}
.firebaseui-container {
  background: rgba(0, 0, 0, 0.05);
  min-height: 150px;
  min-width: 400px;
  border-radius: 5px;
}

#firebaseui_container {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.colour-nav {
  background:rgb(134, 53, 141, 0); 
  background: radial-gradient(circle, rgba(125,77,135, 0.25) 0%, rgba(45, 89, 112, 0.75) 200%);
  padding-bottom: 0px;
  padding-top: 0px
}

.name {
  text-align: center;
  color: white;
  font-family: 'Overpass', sans-serif;
  font-size: 50px;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
}

body {
  padding-top: 90px;
}

.image {
  -webkit-filter: brightness(200%);
          filter: brightness(200%);
  animation-delay: 2s;
  animation-name: rotate;
  animation-duration: 10s;
  animation-iteration-count: infinite;
  animation-timing-function: ease;
}

.nav {
  color: rgb(134, 53, 141);
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
  padding-left: 10px;
  animation-delay: 2s;
  animation-name: colourchange;
  animation-duration: 10s;
  animation-iteration-count: infinite;
  animation-timing-function: ease;
}

.brand {
  padding-left: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

#txt {
  color :rgba(255, 255, 255, 0.815);
  font-size: 18px;
}

#txt-disabled {
  color :rgb(141, 141, 141);
  font-size: 18px;
}

.nav-item {
  padding-right: 10px;
}

.nav-pills .nav-link.active, .nav-pills .show>.nav-link  {
  background:rgb(134, 53, 141);
}

@keyframes rotate {
  0% {transform: rotate(0deg);}
  10% {transform: rotate(180deg);}
  50% {transform: rotate(180deg);}
  60% {transform: rotate(0deg);}
}

@keyframes colourchange {
  0% {-webkit-filter: hue-rotate(0deg);filter: hue-rotate(0deg);}
  10% {-webkit-filter: hue-rotate(210deg);filter: hue-rotate(210deg);}
  50% {-webkit-filter: hue-rotate(210deg);filter: hue-rotate(210deg);}
  60% {-webkit-filter: hue-rotate(0deg);filter: hue-rotate(0deg);}
}

body {
  background-image: url(/website/static/media/signinbackground.bca2a9e0.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center; 
}
